*{margin:0;padding:0}html,body{width:100%;height:100%;background-color:black}canvas{display:block}body{overflow-y:hidden;overflow-x:hidden}#top-bar-container{display:flex;justify-content:space-between;width:auto;height:7vh;background-color:#084c74}#top-bar-container button.clickable:hover{opacity:1;cursor:pointer;box-shadow:0 12px 16px 0 rgba(114,84,84,0.24),0 17px 50px 0 rgba(238,232,232,0.19)}#top-bar-container button.unclickable:hover{opacity:.5;cursor:auto}#title{color:black;font-family:"Lucida Console";align-self:center}#algo-buttons{position:relative;align-self:center}#links{align-self:center}:focus{border:0;outline:0}#button-controls{display:flex;justify-content:center;align-items:center}#button-controls button:not(.unclickable):hover{opacity:1;cursor:pointer;box-shadow:0 12px 16px 0 rgba(114,84,84,0.24),0 17px 50px 0 rgba(238,232,232,0.19)}button{background-color:black;color:white;box-shadow:none;border:0;height:4vh;width:8vw;min-width:90px;opacity:.5;transition:.5s}#linkedin{width:2vw;height:2vw}#github{width:2vw;height:2vw}
